export default {
    data () {
        return {
            content: '',
            totalTime: 10,
            canClick: true //canClick
            // content: '',  // 
            // totalTime: 60      //
        }
    },
    methods: {
        countDown () {
            if (!this.canClick) return 
            this.canClick = false
            this.content = this.totalTime + 's'
            let clock = window.setInterval(() => {
            this.totalTime--
            this.content = this.totalTime + 's'
            if (this.totalTime < 0) {
            window.clearInterval(clock)
            this.content = ''
            this.totalTime = 10
            this.canClick = true  //
            }
            },1000)
        }
    },
    mounted () {}
}
